.params {
	height: 526px;
	width: 250px;
	padding: 0;
	margin: 0 auto;
	border: 1px solid #bbb;
	border-radius: 5px;
	position: relative;
	top: 5px;
	background-color: #fff;
}

.params .header {
	height: 50px;
	padding: 0 25px;
	border-top: 1px solid #bbb;
	background-color: #f8f8f8;
	cursor: pointer;
}

.params .header div{
	text-align: center;
	line-height: 50px;
}

.params .header.b-top {
	border-top: 0;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
}

.params .search {
	width: 100%;
	padding: 0;
	cursor: default;
	border-top: 1px solid #bbb;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	background-color: #f8f8f8;
	height: 50px;
	line-height: 50px;
	position: absolute;
	bottom: 0;
}

.params .search div{
	padding: 7px 10px;
}

.params .search button {
	width: 100%;
	height: 35px;
}

.params .body {
	display: block;
	height: 0;
	border: 0;
	overflow: hidden;
	background-color: #fff;
}

.params .body.active {
	height: 270px;
	border-top: 1px solid #bbb;
	overflow: auto;
}

.select div {
	height: 30px;
	line-height: 30px;
	text-align: center;
	cursor: pointer;
}

.select div:hover {
	background: #ddd;
}

.select div.active {
	background: #337ab7;
	color: #fff;
}

.promo-list .item table {
