 .card {
   position: relative;
   background: #fff;
   box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
   margin-bottom: 30px;
   height: 475px;
 }
 .card-header {
   position: relative;
 }
 @media screen and (min-width: 768px) {
   .card-header:not(.ch-alt) {
	 /*padding: 15px 25px;*/
	 padding: 15px 0;
   }
 }
 @media screen and (max-width: 991px) {
   .card-header:not(.ch-alt) {
	 padding: 18px;
   }
 }
 .card-header h3 {
	font-size: 17px;
 }
 .card-header h2 {
   margin: 0;
   line-height: 100%;
   font-size: 17px;
   font-weight: 400;
 }
 .card-header h2 small {
   display: block;
   margin-top: 5px;
   color: #AEAEAE;
   line-height: 160%;
 }
 @media screen and (min-width: 768px) {
   .card-header.ch-alt {
	 padding: 15px 25px;
   }
 }
 @media screen and (max-width: 991px) {
   .card-header.ch-alt {
	 padding: 18px 18px 28px;
   }
 }
 .card-header.ch-alt:not([class*="bgm-"]) {
   background-color: #f7f7f7;
 }
 .card-header h2,
 .card-heade h2 small {
   color: #000;
 }
 .card-header[class*="bgm-"] h2,
 .card-header[class*="bgm-"] h2 small {
   color: #fff;
 }
 .card-header .actions {
   position: absolute;
   right: 10px;
   z-index: 2;
   top: 15px;
 }
 .card-header .btn-float {
   right: 25px;
   bottom: -23px;
   z-index: 1;
 }
 .card-body {
	display: table;
	/*width: 100%;*/
 }
 .card-body.card-padding {
	padding: 20px 15px;
 }
 .card-header:not(.ch-alt):not([class*="bgm-"]) + .card-padding {
   padding-top: 0;
 }

